import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import "@fortawesome/fontawesome-free/css/all.min.css";
import classNames from "classnames/bind";
import logo from "../../assets/images/logo_bg.png";
import style from "./Navbar.module.scss";
import facebook from "../../assets/images/facebook.svg";
import instagram from "../../assets/images/instagram.svg";

const cx = classNames.bind(style);

function Navbar() {
  const [menuVisible, setMenuVisible] = useState(false);

  // Toggle menu khi nhp vo icon hamburger
  const toggleMenu = () => {
    setMenuVisible(!menuVisible);
  };

  // ng menu khi nhp vo bt k link no
  const closeMenu = () => {
    setMenuVisible(false);
  };

  // ng menu khi thay i kch thc mn hnh
  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth > 767.8) {
        setMenuVisible(false);
      }
    };

    window.addEventListener("resize", handleResize);

    // Dn dp event listener khi component unmount
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  return (
    <div>
      <div className={cx("container")}>
        <div className={cx("navbar")}>
          <div className={cx("menu")}>
            <div className={cx("logo")}>
              <img src={logo} alt="logo" />
            </div>
            <div className={cx("hamburger")} onClick={toggleMenu}>
              <i className="fa-solid fa-bars"></i>
            </div>
            <div className={cx("menu-list")}>
              <Link to="/" className={cx("menu-item")}>
                Home
              </Link>
              <Link to="/portfolio" className={cx("menu-item")}>
                Portfolio
              </Link>
              <Link to="/Contact" className={cx("menu-item")}>
                Contact
              </Link>
              <img src={facebook} alt="facebook" />
              <img src={instagram} alt="instagram" />
            </div>
          </div>
        </div>
        {/* Thm lp "open" khi menuVisible l true */}
        <div className={cx("menu-mobile", { open: menuVisible })}>
          <div className={cx("menu-list-mobile")}>
            {/* Nhp vo link s t ng ng menu */}
            <Link to="/" className={cx("menu-item-mobile")} onClick={closeMenu}>
              Home
            </Link>
            <Link
              to="/portfolio"
              className={cx("menu-item-mobile")}
              onClick={closeMenu}
            >
              Portfolio
            </Link>
            <Link
              to="/contact"
              className={cx("menu-item-mobile")}
              onClick={closeMenu}
            >
              Contact
            </Link>
            <div style={{ display: "flex" }}>
              <div className={cx("menu-social-mobile")}>
                <img src={facebook} alt="facebook" />
                <img src={instagram} alt="instagram" />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className={cx("space")}></div>
    </div>
  );
}

export default Navbar;
